<script>
  import { LayerCake, Html, Svg, uniques, flatten } from 'layercake';
  import { scaleBand } from 'd3-scale';
  import { format } from 'd3-format';

  import csvData from '../data/NewStatesman-EuropeRail-Table.csv';

  import Bar from '../components/GroupedBar.svelte';
  import AxisX from '../components/AxisX.svelte';
  import AxisY from '../components/AxisY.svelte';
  import SearchTable from "./search_table.svelte";

  export let pymChild = {};

  //route,pax,emissions,savings,time

  let tableData = csvData.map(d => ({
    route: d.route,
    pax: format(",.0f")(d.pax),
    emissions: format(",.0f")(d.emissions),
    train: format(",.0f")(d.emissions - d.savings),
    savings: format(",.0f")(d.savings),
    time: d.time
  }))

  export let data = [
    csvData.map(d => ({
      key: d.route,
      value: d.emissions - d.savings
    })),
    csvData.map((d, i) => ({
      key: d.route,
      value: +d.emissions,
      annotation: format(",.0f")(d.pax) + (i === 0 ? " passengers" : "")
    })),
  ];

  console.log(flatten(data));

  const yDomain = uniques(data[0].map(d => d.key));
</script>

<style>
  .svg-container {
    width: 100%;
    height: 400px;
    min-height: 200px;
    padding: 8px;
  }

  .inline-legend {
    display: inline-block;
    padding: 4px;
    border: 1px solid #777;
    background: #adc3d3;
    color: #222;
  }

  .b {
    background: #c1002a;
    color: #fff;
  }
</style>

  <div class="chart-container">
  <h3>Top ten rail routes with biggest carbon dioxide saving</h3>
  <h4><span class="inline-legend">Plane emissions</span> and <span class="inline-legend b">train emissions</span></h4>
  <div class="svg-container">
      <LayerCake
        padding={{ top: 16, bottom: 16, left: 160, right: 16 }}
        x='value'
        y='key'
        yScale={scaleBand().paddingInner([0.2]).round(true)}
        {yDomain}
        xDomain={[0, null]}
        {data}
        flatData={flatten(data)}
      > 
        <Svg>
          <AxisX
            ticks={4}
            gridlines={true}
            baseline={true}
            formatTick={d => format(",.0f")(d)}
          />
          <AxisY
            gridlines={false}
            baseline={true}
            formatTick={d => d}
            hours={csvData.map(d => d.time)}
          />
          <Bar fill={["#c1002a", "#adc3d3"]} />
        </Svg>
      </LayerCake>
  </div>
  <div class="source-text">
    * - non-direct train<br>
    Time is for train, includes check-in time and changes.<br>
    Source: ProRail, Jon Worth</div>
  <SearchTable
    {pymChild}
    headers={[
      {key:"route",
      title:"Route"},
      {key:"time",
      title:"Time for train"},
      {key:"pax",
      title:"Number of passengers"},
      {key:"emissions",
      title:"Air CO2 emissions [kg]"},
      {key:"train",
      title:"Train CO2 emissions [kg]"},
      {key:"savings",
      title:"CO2 savings [kg]"},
    ]}
    data={tableData}
   />
</div>
