<script>
	import { getContext } from 'svelte';

  const { data, xGet, yGet, xScale, yScale } = getContext('LayerCake');
  
  export let fill = ["#00bbff"];
</script>

{#each $data as e, i}
<g class="bar-group">
	{#each e as d, j}
		<rect
			class='group-rect'
			data-id="{j}"
			x="{$xScale.range()[0]}"
			y="{$yGet(d) + ($yScale.bandwidth() / $data.length) * i}"
			height={$yScale.bandwidth() / $data.length}
			width="{$xGet(d)}"
      fill={fill[i]}
		></rect>
	{#if d.annotation}
		<text
			x="{$xScale.range()[0] + 2}"
			y="{$yGet(d) + ($yScale.bandwidth() / $data.length) * 2 - 4}"
		>
		{d.annotation}
		</text>
	{/if}
	{/each}
</g>
{/each}

<style>
	text {
		font-size: 0.725rem;
		fill: #222;
	}
</style>