<script>
	import { getContext } from 'svelte';

	const { padding, xRange, xScale, yScale } = getContext('LayerCake');

	export let ticks = 4;
	export let gridlines = true;
	export let formatTick = d => d;
	export let xTick = 0;
	export let yTick = 0;
	export let dxTick = 0;
	export let dyTick = -4;
	export let textAnchor = 'start';
	export let hours = [];

	console.log(hours);

	$: isBandwidth = typeof $yScale.bandwidth === 'function';

	$: tickVals = Array.isArray(ticks) ? ticks :
		isBandwidth ?
			$yScale.domain() :
			$yScale.ticks(ticks);
</script>

<text
	x='-17.5'
	y='-5'
	text-anchor="middle"
>Time (hours)</text>

<g class='axis y-axis' transform='translate({-$padding.left - 40}, 0)'>
	{#each tickVals as tick, i}
		<g class='tick tick-{tick}' transform='translate({$xRange[0] + (isBandwidth ? $padding.left : 0)}, {$yScale(tick)})'>
			{#if gridlines !== false}
				<line
					x2='100%'
					y1={yTick + (isBandwidth ? ($yScale.bandwidth() / 2) : 0)}
					y2={yTick + (isBandwidth ? ($yScale.bandwidth() / 2) : 0)}
				></line>
			{/if}
			<text
				x='{xTick}'
				y='{yTick + (isBandwidth ? $yScale.bandwidth() / 2 : 0)}'
				dx='{isBandwidth ? -5 : dxTick}'
				dy='{isBandwidth ? 4 : dyTick}'
				style="text-anchor:{isBandwidth ? 'end' : textAnchor};"
			>{formatTick(tick)}</text>

			<rect
				x='{xTick + 20 - 2.5}'
				y='{yTick + (isBandwidth ? $yScale.bandwidth() / 2 : 0) - 14}'
				width="5"
				height="2"
			/>
			<circle r={12} 
				cx='{xTick + 20}'
				cy='{yTick + (isBandwidth ? $yScale.bandwidth() / 2 : 0)}' >
			</circle>
			<text
				x='{xTick + 25}'
				y='{yTick + (isBandwidth ? $yScale.bandwidth() / 2 : 0)}'
				dx='{isBandwidth ? -5 : dxTick}'
				dy='{isBandwidth ? 4 : dyTick}'
				style="text-anchor:middle;"
			>{hours[i] || "-"}</text>
		</g>
	{/each}
</g>

<style>
	text {
		font-size: .725em;
		font-weight: 200;
	}

	.tick line {
		stroke: #aaa;
	}

	.tick text {
		fill: #666;
	}

	.tick.tick-0 line {
		stroke-dasharray: 0;
	}

	circle {
		fill: none;
		stroke: #222;
		stroke-width: 1.25px;
	}
</style>
