<script>
	import { getContext, createEventDispatcher } from 'svelte';
  import { format } from "d3-format";

	const dispatch = createEventDispatcher();

	function setSearch() {
    searchTerm = event.target.value;
  
		dispatch('message', {
			text: searchTerm,
		});
	}
  const defNum = format(",.0f");

  export let pymChild = {};
  export let data = [
    {key: "1979", value: 2},
    {key: "1983", value: 18}
  ];
  export let headers = Object.keys(data[0]).map(d => ({key: d, title: d}));
  export let sortField = "value";
  export let keyField = "key";
  export let keys = data.map(d => d[keyField]);
  export let tableId = "key-list";
  export let enableSearch = false;
  export let defaultOpen = false;
  export let searchTerm = "";
  export let autoSort = true;
  export let sliceSize = 10;
  const sliceMid = sliceSize / 2;

  function updateHeight() {
    setTimeout(() => {
      pymChild.sendHeight()
    }, 50);
  }

  function doSort(b, a) {
    if(autoSort) {
      return b[sortField] - a[sortField]
    }
  }

  data = data.sort(doSort);

  $: searchIdx = data.map(d => d[keyField]).indexOf(searchTerm);
  $: filteredData = (searchTerm === "" || searchIdx < sliceMid) ? data.slice(0, sliceSize) : data.slice(searchIdx - sliceMid, searchIdx + sliceMid);
</script>

<details on:click={() => updateHeight()} open={defaultOpen === true ? 'open' : ''}>
<summary style="{defaultOpen === true ? "display: none;" : ""}"><span>See table</span></summary>
{#if enableSearch}
<input bind:value={searchTerm} on:change={(evt) => setSearch(evt)} list="{tableId}"><button on:click={() => (searchTerm = "")}>Clear</button>
<datalist id="{tableId}">
  {#each keys as key}
    <option value="{key}">{key}</option>
  {/each}
</datalist>
{/if}
<table>
  <thead>
    <tr>
      {#each headers as header}
      <td class={header.type === "number" || header.key === "value" ? "number" : ""}>{header.title}</td>
      {/each}
    </tr>
  </thead>
  {#each filteredData.sort(doSort) as row}
    <tr class="{row[keyField] === searchTerm ? "bold" : ""}">
      {#each headers as header}
        <td class={header.type === "number" || header.key === "value" ? "number" : ""}>
          {#if row[header.key] === "null"}
            No data
          {/if}
          {#if row[header.key] !== "null"}
            {(header.type === "number") ? defNum(row[header.key]) : row[header.key]}
          {/if}
        </td>
      {/each}
    </tr>
  {/each}
</table>
</details>

<style>
  table {
    width: 100%;
    border-collapse: collapse;
  }

  tr:nth-child(odd) {
    background: #e7efff;
  }

  thead {
    font-weight: bold;
  }

  td {
    padding: 2px 4px;
    max-width: 25%;
  }

  .bold {
    font-weight: bold;
  }

  .number {
    text-align: right;
  }

  summary {    
    width: max-content;
    border: 1px solid #333;
    background-color: #f4f4f4;
    padding: 0 4px;
  }
  span {
    padding: 4px;
    display: inline-block;
  }

  details {
    position: relative;
    top: -30px;
  }

  summary {
    float: right;
  }

  @media (max-width: 600px) {
    summary {
      float: none;
    }

    details {
      top: 0;
    }
  }
</style>